<template>
	<movable-area style="width: 100%;height: 100%;">
	<view class="caseAll">
		<!-- 标题栏 -->
		<view class="status-contents">
		    <view class="status top-view"></view>
			  <!-- #ifdef MP-WEIXIN -->
			  <view class="navTitle" :style="{width:widthLength+'rpx'}">
			  	<view class="left" @click="back">
			  		<uni-icons type="arrowleft" size="20" color='#343434'></uni-icons>
			  		<text class="title">{{title}}</text>
			  	</view>
			  	<text @click="help(assitDoc)" class="btnTitle">{{btnTitle}}</text>
			  </view>
			  <!-- #endif -->
			<!-- #ifndef MP-WEIXIN -->
			<view class="navTitle">
				<view class="left" @click="back">
					<uni-icons type="arrowleft" size="20" color='#343434'></uni-icons>
					<text class="title">{{title}}</text>
				</view>
				<text @click="help(assitDoc)" class="btnTitle">{{btnTitle}}</text>
			</view>
			<!-- #endif -->
		</view>
		<view class="wrap">
			<u-tabs-swiper ref="uTabs" :current="current" @change="changeTab" :list="tabs" :bold='false' :is-scroll="false" font-size='24' :show-bar='false' active-color='#86B0D4'></u-tabs-swiper>
			<swiper class="swiper-box" :current="swiperCurrent"  @transition="transition" @animationfinish="animationfinish">
				<swiper-item class="swiper-item">
					<scroll-view style="height: 100%;width: 100%;" scroll-y>
						<!--stl数据的title，三维模型数据 -->
						<electricModel :title='threeModeltitle' :bannerList="threemodel" :Bottomcurrent='swiperCurrent' :dataList='list.threeDModel' @shareModel='share'></electricModel>
						<!-- 医生信息 -->
						<casedocinfor :casedocinfor='casedocinfor'></casedocinfor>
						<view class='section'>
							<!-- 电子病例的十二项的数据，以及就诊历史 -->
							<electricInfor :caseHistory='list' :medicalHistories='list.medicalHistories'></electricInfor>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view style="height: 100%;width: 100%;" scroll-y>
						<!-- 手术方法的所有数据  三维图形的title ,图片，各种匹配情况-->
						<methodsModel :titleName='titleName' :stlId='methodsStlId' :methodsBanner='methodsBanner' :surcigal='surgical' :Alllist='Alllist' :Guidelist='Guidelist' :Instrumentlist='Instrumentlist'  @shareModel='share'></methodsModel>
						<view class='section'>
							<!-- 医生信息 -->
							<casedocinfor :casedocinfor='casedocinfor'></casedocinfor>
							<!-- 十二项数据 -->
							<twelveItems :arrData='surgical'></twelveItems>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view style="height: 100%;width: 100%;" scroll-y>
						<designModel :titleDesign='titleDesign' :stlId='designStlId' :DesignBanner='DesignBanner' :Designmatching='matching' :DesignGuid='DesignGuid' :DesignInstrument='DesignInstrument'  @shareModel='share'></designModel>
						<view class='section'>
							<!-- 医生信息 -->
							<casedocinfor :casedocinfor='casedocinfor'></casedocinfor>
							<caseFiles :files='files'></caseFiles>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view style="height: 100%;width: 100%;" scroll-y>
						<view class='section'>
							<casepredict :stlId='predictStlId' :predictName='predictName' :predicted='casePredict' :casePredictbanner='casePredictbanner' :dataList='casePredict'  @shareModel='share'></casepredict>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view style="height: 100%;width: 100%;" scroll-y>
						<view class='section'>
							<casedocinfor :casedocinfor='casedocinfor'></casedocinfor>
							<casefollow :casefollow='followUpData'></casefollow>
						</view>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
		<!-- 底部 -->
		<view class="">
			<!-- 分别是顶部menu的下标，底部三维数据，3js的stlid -->
			<!-- 电子病例 -->
			<caseBottom v-if="swiperCurrent===0" :stlId='stlId' @caseshare='share' @vrshow='vrshow'></caseBottom>
			<!-- 手术方法 -->
			<caseBottom v-if='swiperCurrent===1' :stlId='methodsStlId' @caseshare='share' @vrshow='vrshow'></caseBottom>
			<!-- 手术设计 -->
			<!-- 匹配情况 导板，器械数据 -->
			<caseBottom v-if='swiperCurrent===2' :stlId='designStlId' @caseshare='share' @vrshow='vrshow'></caseBottom>
			<!-- 效果预测 -->
			<caseBottom v-if='swiperCurrent===3' :stlId='predictStlId' @caseshare='share' @vrshow='vrshow'></caseBottom>
		</view>
		<u-modal v-model="helpshow" width='720' title="树蚁辅助" confirm-color='#000000' negative-top='140rpx' :show-cancel-button='true' @cancel="helpcancel" @confirm="helpconfirm" ref="uModal">
			<view class="slot-content">
				<u-radio-group class="radioGroup" v-model="value" @change="radioGroupChange" active-color='#86B0D4'>
					<view class="radioItem" style="" v-for="(item, index) in listc" :key="index" >
						{{item.name}}
						<u-radio
							:name="item.name"
							:disabled="item.disabled"
						>
						</u-radio>
					</view>
					<uni-transition class="checkbox" :mode-class="['fade']" :show="this.radioId==3" >
						<text>是否需要导板器械设计？（可多选）</text>
						<u-checkbox-group class="checkbox-group">
							<view class="checkItem" v-for="(item, index) in listcheck" :key="index">
								{{item.name}}
								<u-checkbox 
									shape="circle"
									active-color="#86B0D4"
									@change="checkboxChange" 
									v-model="item.checked" 
									:name="item.name"
								></u-checkbox>  
							</view>
						</u-checkbox-group>
					</uni-transition>
				</u-radio-group>
				<view class="switch">
					<text>重建软组织</text>
					<u-switch v-model="checked" active-color="#86B0D4" inactive-color="#CCCCCC" size='30'></u-switch>
				</view>
				<view class="priceTip">
					<text>此项功能将根据服务类型和部位产生相应费用,点击</text>
					<text class="checkprice" @tap="checkprice">查看价格详情</text>
				</view>
			</view>
		</u-modal>
		<!-- vr显示的选择 -->
		<u-popup v-model="groupshow" mode="bottom">
			<view class="groups">
				<view class="modeltitle">
					<text>选择分组设备</text>
				</view>
				<view class="services" v-for="(item, index) in deviceGroup" :key="item.id" >
					<label class="label" @click="select(item.id,item)">
						<view>{{item.name}}</view>
						<view>
							<radio :checked="item.id==radioId" color='#86B0D4' style="transform:scale(0.7)"/>
						</view>
					</label>
					<uni-transition :mode-class="['zoom-in']" :show="item.id==radioId?true:false">
						<scroll-view scroll-y="true" class="serviceradio">
							<view class="items" v-for="(items,index1) in devices" :key='index1'>
								<view class="">
									<view class="servicetitle">
										<text>{{items.deviceName}}</text>
										<view>
											<u-checkbox
												size='28rpx'
												label-size='28rpx'
												style="margin-right: 20rpx;"
												@change="selectread($event,items,radioId)" 
												v-model="items.deviceRead" 
												active-color="#86B0D4"
											>查看权</u-checkbox>
											<u-checkbox
												size='28rpx'
												label-size='28rpx'
												@change="selectoperate($event,items,radioId)" 
												v-model="items.deviceControl" 
												active-color="#86B0D4"
											>操作权</u-checkbox>
										</view>
									</view>
									<view class="servicetitle type">
										<view class="" style="margin-right: 60rpx;">
											<text>类型：</text>
											<text>{{items.deviceIdentity}}</text>
										</view>
										<view class="" style="flex: 1;">
											<text>标识：</text>
											<text>{{items.deviceMark}}</text>
										</view>
									</view>
								</view>
							</view>
						</scroll-view>
					</uni-transition>
				</view>	
				<view class="" style="display: flex;padding: 30rpx 0;border-top: 1px solid #EEEEEE;">
					<text style="flex: 1;text-align: center;" @tap="cancel">取消</text>
					<text style="flex: 1;text-align: center;" @tap='canfirm'>确定</text>
				</view>
			</view>
		</u-popup>
		<auditStatus :auditshow='auditshow' :cancelBtn='cancelBtn' @auditconfirm="auditconfirm" @auditcancel='auditcancel'></auditStatus>
		<!-- 分享的popup -->
		<u-popup v-model="shareshow" mode="bottom"  border-radius="14">
			<view style="padding: 30rpx 30rpx;">
				<text>分享到:</text>
				<view class="" style="display: flex;justify-content: space-around;padding: 30rpx 0;">
					<view class="" style="display: flex;flex-direction: column;line-height: 60rpx;align-items: center;" @click="shareWeixin">
						<!-- #ifdef H5 -->
						<svg class="icon" aria-hidden="true">
						  <use xlink:href='#iconweixin'></use>
						</svg>
						<!-- #endif -->
						<!-- #ifdef APP-PLUS -->
						<image lazy-load src="../../static/image/wechat.png" mode="" style="width: 60rpx; height: 60rpx;"></image>
						<!-- #endif -->
						<text>微信</text>
					</view>
					<view class="" style="display: flex;flex-direction: column;line-height: 60rpx;align-items: center;" @click="shareFriend">
						<!-- #ifdef H5 -->
						<svg class="icon" aria-hidden="true">
						  <use xlink:href='#iconpengyouquan' size='40'></use>
						</svg>
						<!-- #endif -->
						<!-- #ifdef APP-PLUS -->
						<image lazy-load src="../../static/image/friend.png" mode="" style="width: 60rpx; height: 60rpx;"></image>
						<!-- #endif -->
						<text>朋友圈</text>
					</view>
				</view>
				<view class="" style="text-align: center;">
					<text @click="cancelshare">取消</text>
				</view>
			</view>
		</u-popup>
	</view>
	<!-- 密码框 -->
	<u-popup v-model="pwdshow" mode="center" :mask='false' :closeable='true'>
		<view style="background-color: #FFFFFF;border: 1px solid #999;padding: 60rpx 30rpx;">
			<view class="" style="padding: 30rpx 0;">
				<u-field
					:password='true'
					v-model="secondPwd"
					label="密码"
					placeholder="请填写密码"
				>
				</u-field>
			</view>
			<view class="" style="display: flex;justify-content: space-between;padding: 30rpx 0 0;">
				<u-button size="medium" @click="cancelPwd">取消</u-button>
				<u-button type="primary" size="medium" @click="surePwd">确定</u-button>
			</view>
		</view>
	</u-popup>
	<view class="chat" style="position: fixed;bottom: 20%;right: 20%;">
		<movable-view direction='all' style="opacity: .8;">
			<view @click="EnterGroup" class="chatIcon" style="color: #fff;position: relative;display: flex;align-items: center;justify-content: center;width: 60px;height: 60px;border-radius: 50%;background-color: #007AFF;">
				<text v-if="list.relativeGroupId">进入群组</text>
				<text v-else>创建群组</text>
				<u-badge v-if='badgeShow' size="default" :is-dot="true" type="error" :offset='offset'>
				</u-badge>
			</view>
		</movable-view>
	</view>
	</movable-area>
</template>

<script>
	import {getcasedetail} from '@/utill/api/case/getcasedetail.js'
	import {help} from '@/utill/api/case/help.js'
	import {getSurgicalDetail} from '@/utill/api/surgical/getSurgical.js'
	import {helpStatus} from '@/utill/api/case/helpStatus.js'
	import {querydevice} from '@/utill/api/DeviceGroup/querydevice.js'
	import {vrGroup} from '@/utill/api/DeviceGroup/vrGroup.js'
	import {editDevice} from '@/utill/api/DeviceGroup/editDevice.js'
	import casedocinfor from '@/components/case/casedocinfor.vue'
	import electricInfor from '@/components/case/caseElectric/electricInfor.vue'
	import electricModel from '@/components/case/caseElectric/electricModel.vue'
	// 手术方法
	import twelveItems from '@/components/surgical/twelveItems.vue'
	import methodsModel from '@/components/case/caseMethods/methodsModel.vue'
	// 手术设计
	import designModel from '@/components/case/caseDesign/designModel.vue'
	import caseFiles from '@/components/case/caseDesign/caseFiles.vue'
	// 效果预测
	import casepredict from '@/components/case/casepredict/casepredict.vue'
	// 术后随访
	import casefollow from '@/components/case/casefollow/casefollow.vue'
	// 病例底部
	import caseBottom from '@/components/case/caseBottom.vue'
	import {createCaseGroup} from '@/utill/api/connect/connect.js'
	import {getInfor} from '@/utill/api/personalInfor/getInfor.js'
	import {mapState} from 'vuex'
	export default {
		components:{
			// 病例底部
			caseBottom,
			// 主治医生信息
			casedocinfor,
			// 电子病例十二项数据
			electricInfor,
			// 电子病例的模型数据
			electricModel,
			// 手术方法
			twelveItems,
			methodsModel,
			// 手术设计
			designModel,
			caseFiles,
			// 效果预测
			casepredict,
			// 术后随访
			casefollow,
		},
		data() {
			return {
				pwdshow:false,//密码弹窗
				secondPwd:'',
				secondPwdBool:false,
				offset:[20,20], //消息红点
				badgeShow:false,
				menuButtonInfo:{},
				// 顶部导航标题
				title:'详情',
				btnTitle:"树蚁辅助",
				helpshow:false,
				// 寻求帮助列表
				listc: [
					{
						name: '精细三维重建',
						disabled: false,
					},
					{
						name: '模型观察、分析、测量',
						disabled: false
					},
					{
						name: '手术方案设计',
						disabled: false
					}
				],
				checked: false,
				radioId:'',//选中的单选框id
				// u-radio-group的v-model绑定的值如果设置为某个radio的name，就会被默认选中
				value: '',
				listcheck: [
					{
						name: '导板',
						checked: false,
					},
					{
						name: '器械',
						checked: false,
					},
				],
				current: 0,
				background:{
					background:'#f8f8f8'
				},
				fixedBool:true,
				pills:false,
				tabs: [{name: '电子病例'}, 
					{name: '手术方法'}, 
					{name: '手术设计'},
					{name: '效果预测'},
					{name: '术后随访'},
				],
				swiperCurrent: 0,//默认是全部
				list:{},//病例数据
				casedocinfor:[],//主治医生的信息
				id:'',//病例详情的id
				// 电子病例的数据
				threeModeltitle:'',//轮播图顶部的title
				stlId:'',
				threemodel:{},//这个是轮播图对应的模型数据
				// 效果预测
				casePredict:[],
				casePredictbanner:[],//效果预测的轮播图
				predictStlId:'',
				predictName:'',
				// 手术方法
				surgical:{
					threeDGuideList:[
						{stlPicture:'[]'}
					],
					threeDInstrumentList:[
						{stlPicture:'[]'}
					],
					surgicalDesignStepsList:[
						{stlPicture:'[]'}
					],
					preoperativeConditionList:[
						{stlPicture:'[]'}
					],
					postoperativeSituationList:[
						{stlPicture:'[]'}
					]
				},//手术方法的数据	
				Alllist:[],//匹配情况
				Guidelist:[],//导板数据
				Instrumentlist:[],//器械数据
				methodsBanner:[],//手术方法轮播图
				methodsStlId:'',
				titleName:'',//手术方法的title
				// 手术设计
				matching:[],//匹配情况
				DesignGuid:[],//导板数据
				DesignInstrument:[],//器械数据
				DesignData:{},//所有的手术设计数据
				titleDesign:'',//三维数据title
				DesignBanner:{},//三维图形数据
				designStlId:'',
				files:{},//ppt,word 的文件
				// 术后随访
				followUpData:[],
				guideBool:false,
				instrumentBool:false,
				helpstatusBool:'',//手术设计状态
				auditshow:false,//审核状态的展示
				cancelBtn:false,
				shareshow:false,
				casetitle:'病例',//病例的title;
				jstitle:'病例',//3js的title
				casecontent:'病例详情',//转发的内容；
				surfacePic:uni.getStorageSync('uploadLogo'),
				assitDoc:[],//协助医生数据
				groupshow:false,//vr显示的picker
				deviceGroup:[],//设备分组的数据
				radioId:null,
				devices:[],
				readBool:false,
				operateBool:false,
				threeModelId:'',//用于vr设备的选择
				threeModelName:'',//用于vr设备的name
				methodsType:'Matching',//手术方法选择的是导板还是器械,默认是匹配情况
				designType:'Matching',//手术设计选择的导板还是器械还是匹配情况
				shareUrl:'',//分享的3js和整个病例的url
				JsmodelId:'',//用于进行3js分享
				electricId:'',//这个是电子病例的3js文件的id;跟stlid要区分开
				designId:'',//这个是手术设计的3js文件的id;跟stlid要区分开
				shareTitle:'病例',//微信分享的标题
			}
		},
		watch:{
			current(newValue,oldValue){
				this.getData(this.current,this.id)
			},
			'socketData': function(val) {
				if(val=='head'){//心跳回复
					return
				}
				this.badgeShow=true
			}
		},
		methods:{
			// 进入群聊
			EnterGroup(){
				// console.log(this.list)
				// console.log(this.list.relativeGroupId) //病例是否有群组绑定
				// console.log(this.id) //从病例里进群聊，加上group
				// console.log(this.list.caseHistoryNo)//病历号
				this.badgeShow=false
				if(this.list.relativeGroupId){//存在，则直接进入群组中
					uni.navigateTo({ //病例id,病例号，聊天类型
						// id是病例id,otherName 是群组名称，type是所属类型,id是群组id
						url:'/pages/shoyiiChat/chatWindow?caseId='+this.id+'&id='+this.list.relativeGroupId+'&otherName='+this.list.caseHistoryNo+'&type=GROUP'
					})
				}else{//创建群组
					if(this.list.attendingDoctors.length==0||this.list.attendingDoctors[0].doctorId!=uni.getStorageSync('doctorId')){
						uni.showToast({
							title:'非主治医生不能创建群组',
							icon:'none'
						})
						return;
					}
					createCaseGroup({
						name:this.list.caseHistoryNo,
						caseHistoryId:this.id
					}).then(res=>{
						console.log(res)
						if(res.data.code==0){//群组创建成功
							uni.navigateTo({ //病例id,病例号 res.data.object是群组id,聊天类型type
								url:'/pages/shoyiiChat/chatWindow?caseId='+this.id+'&id='+res.data.object+'&otherName='+this.list.caseHistoryNo+'&type=GROUP'
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					}).catch(err=>{
						console.log(err)
					})
				}
			},
			// 转发分享按钮
			share(e){
				// console.log(this.designType)//手术设计选择的是导板还是器械还是匹配情况
				// console.log(this.list.caseHistoryNo)//病历号
				// console.log(this.list.name)//患者名称
				if(this.list.name&&this.list.caseHistoryNo){
					// 病例的详情拼接
					this.casecontent='患者姓名：'+this.list.name+'\n'+'病历医生:'+this.casedocinfor[0].doctorVO.name+'\n'+'病历号:'+this.list.caseHistoryNo
				}else if(this.list.name){
					// 病例的详情拼接
					this.casecontent='患者姓名：'+this.list.name+'\n'+'病历医生:'+this.casedocinfor[0].doctorVO.name
				}else if(this.list.caseHistoryNo){
					// 病例的详情拼接
					this.casecontent='病历医生:'+this.casedocinfor[0].doctorVO.name+'\n'+'病历号:'+this.list.caseHistoryNo
				}else{
					// 病例的详情拼接
					this.casecontent='病例详情'
				}
				// 病例详情
				// console.log(this.casecontent)
				const page=getCurrentPages()
				// console.log(page)
				var length=page.length-1
				var page1 = page[length];				
				// console.log(page1)
				// console.log(page1.route)
				this.pageUrl=page1.route//网页版页面路由
				// console.log(page1.options)//网页版参数
				this.paramter=page1.options.id
				 // #ifdef APP-PLUS
				 this.shareshow=true
				 let curRoute  = this.$mp.page.route;
				 // console.log(curRoute)
				 // console.log(this.$mp.page.options)
				 this.pageUrl=curRoute
				 this.paramter=this.$mp.page.options.id
				 // console.log(this.pageUrl)
				 // console.log(this.paramter)
				 // #endif
				 switch (this.swiperCurrent){
