			// object匹配数据
			changeData(data,id){
				console.log(data)//对应的是匹配情况的数据
				var a=data.filter(item=>{
					if(item.id==id){
						return item
					}
				})
				if(a[0].stlPictureModelIds){
					var b=a[0].stlPictureModelIds.split(',')
					var indexArr=b.map(item=>{//遍历出匹配的下标
						return Object.keys(this.surgical.previewImage).indexOf(item)
					})
					var newArr=[]
					indexArr.filter(item=>{
						newArr.push(Object.values(this.surgical.previewImage)[item])
					})
					this.methodsBanner=newArr
					console.log(this.methodsBanner)
				}	
			},
			getStatus(id){
				helpStatus({
					caseHistoryId:id
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						switch (res.data.object[0].assistDesign){
							case 'YXZ':
								this.helpstatusBool=1
								this.btnTitle='手术协助设计中'
							break;
							case 'WCXZ':
								this.helpstatusBool=2
								this.btnTitle='手术协助已完成'
							break;
							case 'YW':
								this.helpstatusBool=3
								this.btnTitle='手术协助修改中'
							break;
							case 'QD':
								this.helpstatusBool=4
								this.btnTitle='手术协助确定'
							break;
							default:
								this.helpstatusBool=0
								this.btnTitle='树蚁辅助'
							break;
						}
