					console.log(err)
				})
			},
			getData(index,id){
				getcasedetail({
					caseHistoryId:id
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						var data=res.data.object
						this.list=data
						this.casedocinfor=data.attendingDoctors//数组，主治医生有多个？？
						if(data.assistDoctors.length!=0){
							this.assitDoc=data.assistDoctors//协助医生
						}
						switch (index){
							case 0:// 电子病例相关
								if(data.threeDModel.length!=0){
									this.electricId=data.threeDModel[0].id//用于电子病例的3js分享
									this.stlId=data.threeDModel[0].stlFileModelId//stl的id，默认是第一组数据
									this.threemodel=data.threeDModel[0]//用于电子病历的三维数据显示,默认是第一组的
									this.threeModeltitle=data.threeDModel[0].title//用于电子病历的三维数据标题
								}
							break;
							case 1:// 手术方法
								if(!data.video){//video 是个id
									return;
								}
								getSurgicalDetail({//获取数据方法的数据
									surgicalPlanId:data.video,
								}).then(res=>{
									console.log(res)
									var surgicalData=res.data.object
									this.surgical=surgicalData
									if(surgicalData.surgicalPlanMatchingVOS.length!=0){
										this.methodsStlId=surgicalData.surgicalPlanMatchingVOS[0].stlModelIds//获取匹配情况的第一条的methodsStlId；用于默认
										this.titleName=surgicalData.surgicalPlanMatchingVOS[0].name
										// 获取匹配情况的第一条banner数据，在所有的banner中筛选出匹配情况对应的banner图
										this.changeData(surgicalData.surgicalPlanMatchingVOS,surgicalData.surgicalPlanMatchingVOS[0].id)
									}
									this.Alllist=surgicalData.surgicalPlanMatchingVOS//匹配情况
									this.Guidelist=surgicalData.guideVOS//导板匹配情况
									this.Instrumentlist=surgicalData.instrumentVOS//器械匹配情况
								})
							break;
							case 2:// 手术设计
								this.DesignData=data//所有的病例里的数据（器械，导板，匹配情况全部分开）
								if(data.matching&&data.matching.length!=0){
									this.matching=data.matching//匹配情况的数据
									this.titleDesign=data.matching[0].name
									this.designId=data.matching[0].id//匹配情况的对象的id,区别于stl的id
									this.designStlId=data.matching[0].stlModelIds//stl的id,默认是第一个
									this.DesignBanner=data.matching[0].stlPictureUrl//默认是匹配情况的第一条数据
								}
								if(data.guides&&data.guides.length!=0){//导板数据
									this.DesignGuid=data.guides
								}
								if(data.instrument&&data.instrument.length!=0){//器械数据
									this.DesignInstrument=data.instrument
								}
								if(data.surgicalDesignContentDTO){
									this.files=data.surgicalDesignContentDTO//ppt,word 的文件
								}
							break;
							case 3://效果预测
								this.casePredictbanner=[]//每次切换都设置为空
								this.casePredict=data.caseHistoryEffectPredictions
								if(data.caseHistoryEffectPredictions&&data.caseHistoryEffectPredictions.length!=0){
									this.predictStlId=data.caseHistoryEffectPredictions[0].stlModelIds
									this.predictName=data.caseHistoryEffectPredictions[0].name
									console.log(this.casePredict)
									var b=this.casePredict[0].stlPictureModelIds.split(',')
									console.log(b)
									b.map(item=>{//遍历出匹配的下标
										this.casePredict[0].previewList.filter((items)=>{
											if(items.id==item){
												return this.casePredictbanner.push(items)
											}
										})
									})
									console.log(this.casePredictbanner)//效果预测的轮播图
								}
							break;
							case 4://术后随访
								this.followUpData=data.followUps
								console.log(this.followUpData)
							break;
							default:
								if(data.threeDModel.length!=0){
									this.electricId=data.threeDModel[0].id//用于电子病例的3js分享
									this.stlId=data.threeDModel[0].stlFileModelId//stl的id，默认是第一组数据
									this.threemodel=data.threeDModel[0]//用于电子病历的三维数据显示,默认是第一组的
									this.threeModeltitle=data.threeDModel[0].title//用于电子病历的三维数据标题
								}
							break;
						}
					}else {
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
