						this.secondPwdBool=res.data.object.secondLevelPasswordSwitch
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{ 
					console.log(err)
				})
			}
		},
		computed:{
			// #ifdef MP-WEIXIN
			widthLength(){
				return 750 - this.menuButtonInfo.width*2
			},
			// #endif
			...mapState(['socketData'])
		},
		onLoad(options) {
			var auditStatus=this.checkAudit() //审核状态
			if(auditStatus){//审核状态判断
				this.auditshow=true
				return;
			}
			// #ifdef MP-WEIXIN
			this.menuButtonInfo = uni.getMenuButtonBoundingClientRect()
			console.log(this.menuButtonInfo)
			// #endif
			console.log(options.id)
			this.id=options.id
			this.getData(0,options.id)
			this.getStatus(options.id)
			this.getInfor() //用于获取是否设置二级密码
			this.bus.$on('electricData',(data)=>{//监听到电子病例更换的数据
				console.log(data)
				this.threemodel=data//轮播图上的三维数据
				this.threeModeltitle=data.title//轮播图上的三维数据标题
				this.stlId=data.stlFileModelId//轮播图上的三维数据的stlid
				this.electricId=data.id//这个是3js文件的id;跟stlid要区分开
			})
			// 手术方法
			this.bus.$on('methodsData',(data)=>{//监听到电子病例更换的数据
				console.log(data)
				this.methodsBanner=data.bannerList//轮播图上的三维数据
				this.methodsStlId=data.methodsStlId
				this.titleName=data.titleName
				console.log(data.count)
				switch (data.count){
					case 0:
					 this.methodsType='Matching'
					break;
					case 1:
					 this.methodsType='GuidePlate'
					break;
					case 2:
					 this.methodsType='Instrument'
					break;
					default:
					break;
				}
				console.log(data.count)
			})
			// 手术设计
			this.bus.$on('designData',(data)=>{//监听到电子病例更换的数据
				console.log(data)
				this.DesignBanner=data.DesignBanner//轮播图上的三维数据
				this.designStlId=data.designStlId
				this.titleDesign=data.titleDesign
				this.designId=data.designId//获取的整个导板或器械或匹配情况id
				console.log(data.count)
				switch (data.count){
					case 0:
					 this.designType='Matching'
					break;
					case 1:
					 this.designType='GuidePlate'
					break;
					case 2:
					 this.designType='Instrument'
					break;
					default:
					break;
				}
			})
			// 效果预测
			this.bus.$on('predictData',(data)=>{//监听到电子病例更换的数据
				console.log(data)
				this.casePredictbanner=data.predictData//轮播图上的三维数据
				this.predictName=data.name//轮播图上的三维数据标题
				this.predictStlId=data.stlId//轮播图上的三维数据的stlid
			})	
		},
	}
</script>

